.Datalogsection {
    position: absolute; /* 부모 컨테이너 기준으로 고정된 위치 */
    width: 41.67vw; /* 너비 설정 */
    height: 30vh; /* 높이 설정 */
    background: grey; /* 배경색 설정 */
    top: 10.56vh; /* 부모 컨테이너의 기준 위치 설정 */
    right: 1vw; /* 부모 컨테이너의 기준 위치 설정 */
    padding: 10px; /* 내부 여백 추가 */
    border-radius: 8px; /* 모서리 둥글게 */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); /* 그림자 추가 */
}

.Datalogsection h1 {
    font-size: 1.5em; /* 제목 크기 설정 */
    color: white; /* 제목 색상 설정 */
    margin: 0 0 10px; /* 제목 하단 여백 설정 */
}

.Datalogsection p {
    font-size: 1.2em; /* 텍스트 크기 설정 */
    color: white; /* 텍스트 색상 설정 */
    margin: 5px 0; /* 텍스트 상하 여백 설정 */
}