.App {
  text-align: center;
}

body {
  font-family: 'nanumsquare', sans-serif;
  overflow: auto;
  margin: 0; /* 기본 여백 제거 */
  padding: 0; /* 기본 패딩 제거 */
  background-color: #f8f9fa; /* 부드러운 배경색 추가 */
}

.black-nav {
  position: fixed;
  background: black;
  width: 100%;
  height: 60px; /* 높이 증가 */
  display: flex;
  align-items: center; /* 수직 중앙 정렬 */
  color: white;
  padding: 0 20px; /* 좌우 패딩 조정 */
  font-weight: 600;
  font-size: 24px; /* 글꼴 크기 증가 */
  z-index: 1;
}

.container {
  margin-top: 70px; /* 네비게이션 바 아래 여백 */
}

button {
  cursor: pointer; /* 버튼에 커서 포인터 추가 */
}

/* 필요에 따라 추가 스타일 조정 */