.buttonsection {
  position: absolute; /* 부모 컨테이너 기준으로 고정된 위치 */
  width: 41.67vw;
  height: 52.30vh;
  background: yellow;
  top: 42.04vh; /* 400px에 해당하는 top 위치 */
  right: 1vw;
}

.buttons {
  position: relative; /* 상대 위치로 변경하여 레이아웃 간섭 방지 */
  width: 18.23vw;  /* 약 350px에 해당하는 너비 */
  height: 20.52vh; /* 약 200px에 해당하는 높이 */
  background-color: #007bff;
  font-size: 7.41vh;  /* 약 80px에 해당하는 폰트 크기 */
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* 더 뚜렷한 그림자 효과 */
  transition: transform 0.2s, box-shadow 0.2s; /* 그림자 변화도 추가 */
}

.btn-1 {
  top: 2.51vh;   /* 25px에 해당하는 top 위치 */
  left: 0.30vw;  /* 25px에 해당하는 left 위치 */
}

.btn-2 {
  top: 2.51vh;   /* 25px에 해당하는 top 위치 */
  left: 2.14vw; /* 425px에 해당하는 left 위치 */
}

.btn-3 {
  top: 8.76vh;  /* 275px에 해당하는 top 위치 */
  left: 0.30vw;  /* 25px에 해당하는 left 위치 */
}

.btn-4 {
  top: 8.76vh;  /* 275px에 해당하는 top 위치 */
  left: 2.14vw; /* 425px에 해당하는 left 위치 */
}

.buttons:hover {
  transform: scale(1.05); /* 약간의 크기 증가 */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Hover 시 그림자 변화 */
}

.buttons:active {
  transform: scale(0.95); /* 클릭 시 크기 감소 */
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2); /* 클릭 시 그림자 감소 */
}