.container {
    position: relative; /* 부모 컨테이너 기준으로 설정 */
    width: 100%;  /* 원하는 너비 설정 */
    height: 100%; /* 원하는 높이 설정 */
}

.video {
    position: absolute; /* 비디오를 절대 위치로 설정 */
    top: 10.56vh; /* 부모 컨테이너의 기준 위치 설정 */
    left: 1vw; /* 부모 컨테이너의 기준 위치 설정 */
    width: 52.08vw; /* 비디오의 너비 */
    height: 84.07vh; /* 비디오의 높이 */
}

.detections {
    position: absolute; /* 감지 결과를 절대 위치로 설정 */
    top: 5vh; /* 위치 설정 */
    left: 60vw; /* 위치 설정 */
    max-height: 90vh; /* 최대 높이 설정 */
    overflow-y: auto; /* 세로 스크롤 추가 */
    background-color: rgba(255, 255, 255, 0.8); /* 배경색 설정 */
    padding: 10px; /* 패딩 추가 */
    border-radius: 5px; /* 테두리 둥글게 */
}